<template>
  <div class="marco">
     <div class="aireTopGeneral"></div>
     <b-container class="fuente">
        <b-row>
                <b-col cols="12" sm="4" md="3" lg="2" xl="2" class="text-center">
                      <img src="../assets/MiFoto_Avatar.svg" class="d-inline-block align-top tamanioFoto rounded-circle" alt="JuanmaCaballero">
                </b-col>
                <b-col cols="12" sm="8" md="9" lg="10" xl="10" class="text-left aireIzquierda">
                  <p>La descripción de mi perfil podría ser así...<i>Desarrollador Front-end con mas de 4 años de experiencia en
                    producción de sitios web de alta calidad.</i>
                  </p>
                </b-col>
        </b-row>
         <b-row>
                <b-col cols="12" class="text-left">
                  <p>De todas formas, dentro de ese título hay mucha más historia. Empecé en 2005 en una beca
                    en el mundo del Host llegando mi aventura hasta casi los 10 años de experiencia. 'Mala vida' llevé
                    con evolutivos y mantenimiento de procesos BATCH, interpretando ficheros en hexadecimal y navegando entre errores típicos de los famosos
                    JCL; 0C7,U4038 y un largo etcétera entre otras cosas. Sería injusto decir que no hubo fantásticos momentos
                    de aprendizaje en el entorno de banca y seguros.
                  </p>
                </b-col>
        </b-row>
         <b-row>
                <b-col cols="12" class="text-left">
                  <p>Dentro de este gran recorrido ya iba meditando que quería salir de la zona de confort,
                    afrontar nuevos retos y cambiar completamente de tecnología,
                    por lo que inicié mis pasos en el mundo frontend. Como os podeis imaginar, si alguien ha pasado
                    por este tipo de cambio llega a desesperar y a meditar, ¿pero quién me manda meterme a mí en estos líos?
                    , pero la respuesta es clara y contundente, <strong>PASIÓN POR LAS NUEVAS TECNOLOGÍAS</strong>.
                  </p>
                </b-col>
        </b-row>
          <b-row>
                <b-col cols="12" class="text-left">
                  <p>Es verdad que me queda mucho camino por recorrer y mas que aprender pero sinceramente puedo
                    decir que soy una persona que pone empeño y dedicación a su trabajo.
                  </p>
                </b-col>
        </b-row>
      </b-container>
  </div>
</template>

<script>
export default {
  name: 'quiensoy'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style rel="stylesheet/scss" lang="scss" scoped>
@import "src/styles/index.scss";

.fuente{
  font-family: "Lato light";
  text-indent: 1em;
}

.marco {
    border-left: 8px solid white;
    border-right: 8px solid white;
    background: -webkit-linear-gradient(white, lightgrey, white);
    background: -moz-linear-gradient(white, lightgrey, white);
    background: -o-linear-gradient(white, lightgrey, white);
    background: linear-gradient(white, lightgrey, white);
}

/*#quiensoy {
  background: url("../assets/fondo_quiensoy.svg");
  background-repeat: no-repeat;
}*/

@media only screen and (max-width: 576px){
  .aireIzquierda {
     padding-top: 1em;
  }

 }

@media only screen and (min-width: 576px) and (max-width: 768px){
  .aireIzquierda {
     padding-top: 1em;
     padding-left: 1em;
  }

 }

 @media only screen and (min-width:768px) and (max-width: 1200px){
  .aireIzquierda{
    padding-top: 2em;
    padding-left: 3em;
  }

 }

  @media only screen and (min-width:1200px){
  .aireIzquierda{
    padding-top: 2em;

  }

 }

</style>



